import Vue from 'vue'
import Router from 'vue-router'
import RemoteSoluation from '@/views/RemoteSoluation'
import Home from '@/views/Home'
import BmiBar from '@/views/data/bmi/BarChart'
import BmiMap from '@/views/data/bmi/Map'
import BmiLine from '@/views/data/bmi/LineChart'
import HeartBar from '@/views/data/heartFailure/BarChart'
import HeartMap from '@/views/data/heartFailure/Map'
import HeartLine from '@/views/data/heartFailure/LineChart'
import WorkBar from '@/views/data/workLoad/BarChart'
import WorkMap from '@/views/data/workLoad/Map'
import WorkLine from '@/views/data/workLoad/LineChart'
import PageHome from '@/views/data/home/PageHome'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: Home,
        redirect: '/pagehome',
        children: [{
                path: '/remoteSoluation',
                name: 'RemoteSoluation',
                component: RemoteSoluation,
            },
            //BMI
            {
                path: '/bmi/barChart',
                name: 'BarChart',
                component: BmiBar,
            },
            {
                path: '/bmi/map',
                name: 'MapChart',
                component: BmiMap,
            },
            {
                path: '/bmi/lineChart',
                name: 'LineChart',
                component: BmiLine,
            },
            //Heart
            {
                path: '/heart/barChart',
                name: 'HeartBar',
                component: HeartBar,
            },
            {
                path: '/heart/map',
                name: 'HeartMap',
                component: HeartMap,
            },
            {
                path: '/heart/lineChart',
                name: 'HeartLine',
                component: HeartLine,
            },
            //work
            {
                path: '/work/barChart',
                name: 'WorkBar',
                component: WorkBar,
            },
            {
                path: '/work/map',
                name: 'WorkMap',
                component: WorkMap,
            },
            {
                path: '/work/lineChart',
                name: 'WorkLine',
                component: WorkLine,
            },
            //
            {
                path: '/pagehome',
                name: 'PageHome',
                component: PageHome,
            }
        ]
    }]
})


